
import { IPackageInfo, appPackagesStore } from "@app/static";
import { autoWebViewJs } from "../";
import { isRealInAndroid } from "../is-in-android";
// import { readAllPackagesMockData } from "./mock-data/readAllPackages";
const script = `com.fanfanlo.droid.js.JsReceiver.readAllPackages();`

function readAllPackages():IPackageInfo[]{
    let res = autoWebViewJs.callScript<IPackageInfo[]>(script);
    const list =  (res.javaResultData || []) as IPackageInfo[]
    appPackagesStore.savePackages(list)
    return list
}
async function mockReadAllPackages():Promise<IPackageInfo[]>{
    const list = [
        {
            label: "test",
            iconBase64: "test",
            packageName: "test"
        }
    ]

    // if(appPackagesStore.data.list.length > 0)return appPackagesStore.data.list
    const mockData = await import('./mock-data/readAllPackages')
    appPackagesStore.savePackages(mockData.readAllPackagesMockData as IPackageInfo[])
    return mockData.readAllPackagesMockData
}

export const readAllPackagesScript = {
    script,
    readAllPackages: isRealInAndroid ? readAllPackages : mockReadAllPackages,
}
