import { ActionOrTriggerListTypeEnum, appPackagesStore, IActionOrTriggerListPageParams, IPackageInfo, routerUtils } from "@app/static";
import { readAllPackagesScript } from "@droid/android/";
import { proxyWatch, storeUtils, throwTraceError, toProxy, useProxyWatch, useTranslation } from "@fanfanlo";
import RefreshIcon from '@mui/icons-material/Refresh';
import SearchIcon from '@mui/icons-material/Search';
import { alpha, Box, Grid2, IconButton, InputBase, List, ListItem, ListItemText, styled, Tab, Tabs } from "@mui/material";
import { useRouter } from "next/router";
import { createContext, useContext, useEffect, useState } from "react";
import { AppbarContainer, AppShell } from "@app/ui";
import { getTriggerOrActionListPageParams, ProjectMemberType } from "@droid/project-interface";
import { actionOpenAppUtils, getActionUtils, IActionOpenAppData } from "@droid/project-interface";
import { projectEventUtils } from "@droid/project-interface";
const storeName = 'app-selector'
const storage = storeUtils.namespace(storeName)
const storeRecentlyPackageListKey = 'recentlyPackageList'

enum TabEnum {
  all = "all",
  recently = "recently"
}
interface IPageProps {
  searchKey?: string
  packageList: IPackageInfo[]
  showPackageList: IPackageInfo[]
  recentlyPackageList: IPackageInfo[]
  tab: TabEnum
  onPackageSelect: (packageInfo: IPackageInfo) => void
}

const PageContext = createContext<IPageProps>({
  packageList: [],
  showPackageList: [],
  recentlyPackageList: [],
  tab: TabEnum.all,
  onPackageSelect: (packageInfo: IPackageInfo) => { }
})
function useController() {
  const router = useRouter()
  const nowPath = router.pathname;
  function onPackageSelect(packageInfo: IPackageInfo) {
    addRecentlyPackage(packageInfo)
    handlePackageSelect(packageInfo)
  }

  function handlePackageSelect(packageInfo: IPackageInfo) {
    const query = router.query as IActionOrTriggerListPageParams
    switch (query.type) {
      case ActionOrTriggerListTypeEnum.addAction:
        {
          const res = getTriggerOrActionListPageParams(query)
          if (!res.data.actionGroup) {
            console.error('actionGroup is undefined query=', query, 'packageInfo=', packageInfo)
            throw new Error('actionGroup is undefined')
          }
          if(!query.actionType){
            return throwTraceError(`actionType is undefined query=${query}`, query, packageInfo)
          }
          // const utils = ActionUtilsMap[query.actionType]
          const utils = getActionUtils<IActionOpenAppData>(query.actionType)
          if (!utils) throw new Error(`actionType ${query.actionType} is not found`)
          const action = utils.createDefultData()
          switch (query.actionType) {
            case actionOpenAppUtils.type:
              action.javaData.packageName = packageInfo.packageName
              break
            default:
              throw new Error(`actionType ${query.actionType} is not found`)
          }
          // projectEventUtils.dispatchAddEvent(action.type, ProjectMemberType.actionGroup, action, res)

          projectEventUtils.dispatchEditEvent(projectEventUtils.EditType.add, {
            projectMemberType:ProjectMemberType.actionGroup,
            data:action,
            projectInfo:res.data
          })
          // res.data.actionGroup.list.push(action)
          routerUtils.checkToPath(router, nowPath, query.backPath!, 2)
          // router.back()
          // router.back()
          break
        }
      case ActionOrTriggerListTypeEnum.updateAction: {
        const res = getTriggerOrActionListPageParams(query)
        console.log('res', res)
        if (!res.data.actionGroup) {
          console.error('actionGroup is undefined query=', query, 'packageInfo=', packageInfo)
          throw new Error('actionGroup is undefined')
        }
        if(!res.data.action){
          console.error( `action is undefined query= `, query, `packageinfo=`, packageInfo)
          throw new Error(`action is undefined`)
        }

          projectEventUtils.dispatchEditEvent(projectEventUtils.EditType.updateJavaData, {
            projectMemberType:ProjectMemberType.action,
            data:{packageName:packageInfo.packageName},
            projectInfo:res.data
          })
        console.log('dispatch event232323456')
        switch (query.actionType) {
          case actionOpenAppUtils.type:
            // const action: IActionOpenAppData = res.data.action as IActionOpenAppData
            // action.javaData.packageName = packageInfo.packageName
            router.back()
            break
          default:
            throw new Error(`actionType ${query.actionType} is not found`)
        }
        break
      }

    }
  }
  function addRecentlyPackage(packageInfo: IPackageInfo) {
    const index = props.recentlyPackageList.findIndex(item => item.packageName === packageInfo.packageName)
    if (index !== -1) {
      props.recentlyPackageList.splice(index, 1)
    }
    props.recentlyPackageList.unshift(packageInfo)
    storage.write(storeRecentlyPackageListKey, props.recentlyPackageList)
  }
  const props = toProxy<IPageProps>({
    packageList: [],
    showPackageList: [],
    recentlyPackageList: storage.read(storeRecentlyPackageListKey) || [],
    tab: TabEnum.all,
    onPackageSelect
  })
  proxyWatch(props, 'tab', (v, old) => {
    updateShowPackageList()
  })
  proxyWatch(props, 'searchKey', (v, old) => {
    updateShowPackageList()
  })
  function updateShowPackageList() {
    props.showPackageList = [...getShowPackageList()]
  }
  function getTabShowPackageList() {
    if (props.tab === TabEnum.all) {
      return props.packageList
    } else if (props.tab === TabEnum.recently) {
      return props.recentlyPackageList
    }
    throw new Error("tab is not valid")
  }
  function getShowPackageList() {
    const list = getTabShowPackageList()
    if (props.searchKey) {
      return list.filter(item => item.label.includes(props.searchKey!!))
    }
    return list
  }

  proxyWatch(props, 'searchKey', (v, old) => {
    updateShowPackageList()
  })

  proxyWatch<{ list: IPackageInfo[] }, IPackageInfo[]>(appPackagesStore.data, 'list', (v, old) => {
    props.packageList = [...appPackagesStore.data.list]
    updateShowPackageList()
  })
  useEffect(() => {
    if (props.packageList.length > 0) return //不为空则跳出
    appPackagesStore.loadPackages()//从缓存读取
    if (appPackagesStore.data.list.length > 0) return //缓存不为空则跳出
    readAllPackagesScript.readAllPackages()//从安卓读取
  }, [])
  updateShowPackageList()
  return [props]
}
function AppList() {
  const pageProps = useContext(PageContext)
  const [list] = useProxyWatch<IPageProps, IPackageInfo[]>(pageProps, 'showPackageList', pageProps.showPackageList)
  return (
    <Box>
      <List>
        {list.map((item, index) => (
          <ListItem key={index} onClick={() => pageProps.onPackageSelect(item)}>
            <Grid2 container>
              <Grid2 size="grow" container
              data-id="app-selector-893f">
                <img width={30}
                  style={{ borderRadius: '8px' }}
                  src={`data:image/gif;base64,${item.iconBase64}`}
                />
                <ListItemText primary={item.label} />
              </Grid2>
            </Grid2>
          </ListItem>
        ))}
      </List>
    </Box>
  )
}


function AccessibleTabs() {
  const [value, setValue] = useState(0);
  const { t } = useTranslation("homepage/pages/action-list/app-selector/content")
  const handleChange = (event: React.SyntheticEvent, newValue: number) => {
    setValue(newValue);
    pageProps.tab = newValue === 0 ? TabEnum.all : TabEnum.recently
  };
  const pageProps = useContext(PageContext)
  return (
    <Box sx={{ width: '100%' }}>
      <Tabs sx={{ width: '100%' }}
        onChange={handleChange}
        value={value}
        aria-label="Tabs where selection follows focus"
        selectionFollowsFocus
      >
        <Tab sx={{ width: '50%' }} label={t("content.all")} />
        <Tab sx={{ width: '50%' }} label={t("content.recently")} />
      </Tabs>
    </Box>
  );
}

const Search = styled('div')(({ theme }) => ({
  position: 'relative',
  borderRadius: theme.shape.borderRadius,
  backgroundColor: alpha(theme.palette.common.white, 0.15),
  '&:hover': {
    backgroundColor: alpha(theme.palette.common.white, 0.25),
  },
  marginLeft: 0,
  width: '100%',
  [theme.breakpoints.up('sm')]: {
    marginLeft: theme.spacing(1),
    width: 'auto',
  },
}));

const SearchIconWrapper = styled('div')(({ theme }) => ({
  padding: theme.spacing(0, 2),
  height: '100%',
  position: 'absolute',
  pointerEvents: 'none',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
}));

const StyledInputBase = styled(InputBase)(({ theme }) => ({
  color: 'inherit',
  width: '100%',
  '& .MuiInputBase-input': {
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(4)})`,
    transition: theme.transitions.create('width'),
    [theme.breakpoints.up('sm')]: {
      width: '12ch',
      '&:focus': {
        width: '20ch',
      },
    },
  },
}));

function Title() {
  const { t } = useTranslation("homepage/pages/action-list/app-selector/content")
  const pageProps = useContext(PageContext)
  function onSeachChange(e: React.ChangeEvent<HTMLInputElement>) {
    pageProps.searchKey = e.target.value
  }
  return (
    <Grid2 container width={"100%"} justifyContent={"space-between"}
      sx={{ alignItems: "center" }}>
      <Grid2>

        <Search>
          <SearchIconWrapper>
            <SearchIcon />
          </SearchIconWrapper>
          <StyledInputBase
            onChange={onSeachChange}
            placeholder={t("content.searchPlaceholder")}
            inputProps={{ 'aria-label': 'search' }}
          />
        </Search>
      </Grid2>
      <Grid2>
        <IconButton onClick={() => {
          readAllPackagesScript.readAllPackages()
        }}>
          <RefreshIcon />
        </IconButton>

      </Grid2>
    </Grid2>
  )
}
export default function AppSelector() {
  const [props] = useController()
  return (
    <AppShell>
      <PageContext.Provider value={props}>
        <AppbarContainer
        titleConf={{ns:"homepage/pages/action-list/app-selector/content", key:"content.title"}}
        appbarProps={{
          title: <Title />,
          back: true,
      }}>
        <AccessibleTabs />
        <AppList />
      </AppbarContainer>
    </PageContext.Provider>
    </AppShell>
  )
}


